<script lang="ts">
  import { writable } from "svelte/store";

  import drag from "../utils/drag";
  import project from "../stores/project";
  import uiState from "../stores/ui";
  import FlexSpace from "./FlexSpace.svelte";
  import NewTrackHead from "./NewTrackHead.svelte";
  import SplitBar from "./SplitBar.svelte";
  import SplitPane from "./SplitPane.svelte";
  import TrackHead from "./TrackHead.svelte";
  import VStack from "./VStack.svelte";

  let tracklist: HTMLElement;
  let scroll = writable({
    x: 0,
    y: 0,
  });

  $: if (tracklist) {
    tracklist.scrollTop = $scroll.y;
    // the user-agent clamps scrollTop for us... tysm user-agent :)
    $scroll.y = tracklist.scrollTop;
  }

  $: $scroll.x = Math.max(0, $scroll.x);
</script>

<style lang="scss">
  .tracklist {
    display: grid;
    overflow: hidden;

    .content {
      flex-grow: 1;
      display: grid;
    }
  }
</style>

<div
  bind:this={tracklist}
  class="tracklist"
  use:drag={{ button: 1, offset: scroll }}>
  <SplitPane
    direction="row"
    min={150}
    snaps={[150, 200, 250]}
    bind:splitpos={$uiState.trackHeadsWidth}>
    <VStack spacing={1}>
      <VStack spacing={2}>
        {#each $project.tracks as track}
          <TrackHead bind:track />
          <SplitBar
            bind:position={track.height}
            snaps={[1, 2, 3, 4].map((x) => 64 * x)}
            min={64} />
        {/each}
      </VStack>
      <FlexSpace />
      <NewTrackHead />
    </VStack>
    <div class="content">
      <slot xscroll={$scroll.x} />
    </div>
  </SplitPane>
</div>
